import React, { Component } from 'react'
import {
    Segment,
    Header,
    Item,
    Button,
    Icon,
    Label
  } from 'semantic-ui-react'

export default class ItineraryDay extends Component {
    render() {
        return(
            <Segment vertical raised style={{ minHeight: 200, padding: '1em', margin: '0.5em 0em' }}>
                <Header as='h3'>{this.props.toto.title}</Header>
                <Item.Group>
                    <Item>
                        <Item.Image size='small' src='/assets/images/items/image.png' />

                        <Item.Content>
                            <Item.Header as='a'>{this.props.toto.attraction1}</Item.Header>
                            <Item.Meta>
                            <span className='cinema'>Union Square 14</span>
                            </Item.Meta>
                            <Item.Description>Sultanahmet Square is a nice par</Item.Description>
                            <Item.Extra>
                            <Label>IMAX</Label>
                            <Label icon='globe' content='Additional Languages' />
                            </Item.Extra>
                        </Item.Content>
                    </Item>

                    <Item>
                    <Item.Image size='small' src='/assets/images/wireframe/image.png' />

                    <Item.Content>
                        <Item.Header as='a'>My Neighbor Totoro</Item.Header>
                        <Item.Meta>
                        <span className='cinema'>IFC Cinema</span>
                        </Item.Meta>
                        <Item.Description>Sultanahmet Square is a nice par</Item.Description>
                        <Item.Extra>
                        <Button primary floated='right'>
                            Buy tickets
                            <Icon name='right chevron' />
                        </Button>
                        <Label>Limited</Label>
                        </Item.Extra>
                    </Item.Content>
                    </Item>

                    <Item>
                    <Item.Image size='small' src='/assets/images/wireframe/image.png' />

                    <Item.Content>
                        <Item.Header as='a'>Watchmen</Item.Header>
                        <Item.Meta>
                        <span className='cinema'>IFC</span>
                        </Item.Meta>
                        <Item.Description>Sultanahmet Square is a nice par</Item.Description>
                        <Item.Extra>
                        <Button primary floated='right'>
                            Buy tickets
                            <Icon name='right chevron' />
                        </Button>
                        </Item.Extra>
                    </Item.Content>
                    </Item>
                </Item.Group>
            </Segment>
        )
    }
}


